<template>
  <div class="list-content">
    <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        >
            <van-cell v-for="(item,i) in list" :key="i"  @click="jinruxq(item._id)">
              <div class="content1" v-if="item.imageSrc.length>1">
                 <h3>{{item.title}}</h3>
                 <div class="content-img">
                    <div class="content-div" v-for="(item1,i) in item.imageSrc" :key="i">
                         <img :src="item1" alt="" @error="imgError"/>
                    </div>
                 </div>
                 
              </div>
              <div class="content" v-else>
                 <h3>{{item.title}}</h3>
                 <div class="content-img" v-for="(item1,i) in item.imageSrc" :key="i">
                    <img :src="item1" @error="imgError" alt=""/>
                 </div>
              </div>
              <div class="content-bottom">
                <p>作者{{item.author}}</p>
                <p>{{item.comment}}评论</p>
                <p>发布时间{{ontime(item.time)}}</p>
                <div>
                    <van-icon name="cross"  @click.stop="del(item._id)"/> 
                </div>
              </div>
            </van-cell>
      </van-list>
  </div>
</template>

<script>
import axios from 'axios'
import { Icon,Tab, Tabs,List,Cell,Toast,Dialog  } from 'vant';
export default {
  components:{
    [Icon.name]:Icon,
    [Tab.name]:Tab,
    [Tabs.name]:Tabs,
    [List.name]:List,
    [Cell.name]:Cell,
    [Toast.name]:Toast,
    [Dialog.name]:Dialog  
 },
 data() {
  return {
        loading: false,
        finished: false,
        list:[],
        skip:0,
        uid:localStorage.getItem('uid')
        
  }
 },
 methods:{ 
  onLoad() {
      // 异步更新数据
      axios({
            method:'get',
            url:`https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_user_article_list?uid=${this.uid}&skip&limit`,
            }).then(res=>{
                this.list=res.data.data
                console.log(res);
                 // 加载状态结束
                this.loading = false;

                // 数据全部加载完成
                if (this.list.length >= res.data.count){
                  this.finished = true;
                };
      })
        // console.log("123",this.skip);
       
    },
    jinruxq(id){
      this.$router.push({
          name:'listcontent',
          query:{
            id:id
          }
      })
    },
    del(id){
      // 删除发布
      Dialog.confirm({
          title: '是否确认删除',
          })
          .then(() => {
           this.$axios({
              method:'post',
              url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/del_user_article',
              data:{
                '_id':id,
                'uid':this.uid
              }
            }).then(res=>{
              Toast(res.data.msg)
              this.onLoad()
              // console.log(res);
            })
          })
          .catch(() => {
            // on cancel
            // console.log(2);
          });
      
    }
     
 },
 mounted() {
    this.onLoad()
  }
}
</script>

<style lang="less" scoped>
.list-content{
  margin-bottom: 50px;
  .van-list{
    margin-top: 50px;
  }
  .van-icon{
    font-size: 15px;
  }
}
.content{
    display: flex;
    justify-content: space-between;
    .content-img{
        width: 100px;
        img{
            width: 100%;
            height:auto;
            }
    }
   
}
.content1{
    
    .content-img{
        // width: 375px;
        display: flex;
        justify-content: space-around;
        .content-div{
            width: 100px;
            img{
                width: 100%;
                height:auto;
                
            }
        }
        
    }
   
}
.content-bottom{
    display: flex;
    justify-content: space-around;
    font-size: 8px;
}
</style>